import React from 'react';
import ReactDom from 'react-dom';
import Card from './Card';
import Sdata from './Sdata';
import './index.css';

ReactDom.render(
    <>
    <h1 className="heading">List Of Top Netflix Orignal Series</h1>
    
    {Sdata.map((val, index) => {
        return(
            <Card 
            imgsrc = {val.imgsrc}
            title = {val.title}
            sname = {val.sname}
            link = {val.link}
            />
        )
    })}

    </>,
    document.getElementById('root')
);

